.section-competences {
  background: #ececec;
  margin: 0;
  padding: 50px 0 150px;
  width: 100%;

  .competences-title {
    font-size: 35px;
    color: #315c99;
    padding-bottom: 50px;
  }

  .container {
    position: relative;
    max-width: 500px;
    background: #ececec;
    padding: 10px 20px;
    border-radius: 7px;
    user-select: none;
    margin: auto;

    .skill-box {
      width: 100%;
      margin: 25px 0;

      .title {
        display: block;
        font-size: 18px;
        font-weight: 600;
        color: #333;
        padding-bottom: 30px;

        img {
          padding-right: 5px;
          transform: translateY(3px);
        }
      }

      .skill-bar {
        height: 8px;
        width: 100%;
        border-radius: 6px;
        margin-top: 15px;
        background: rgba(0, 0, 0, 0.1);

        .skill-per {
          position: relative;
          display: block;
          height: 100%;
          width: 90%;
          border-radius: 6px;
          background: #315c99;
          animation: progress 0.4s ease-in-out forwards;
          opacity: 0;

          &.html {
            width: 80%;
            animation-delay: 0.1s;
          }

          &.css {
            width: 70%;
            animation-delay: 0.1s;
          }

          &.javascript {
            width: 50%;
            animation-delay: 0.2s;
          }

          &.react {
            width: 50%;
            animation-delay: 0.3s;
          }

          .tooltip {
            position: absolute;
            right: -14px;
            top: -28px;
            font-size: 9px;
            font-weight: 500;
            color: #fff;
            padding: 2px 6px;
            border-radius: 3px;
            background: #315c99;
            z-index: 1;

            &::before {
              content: "";
              position: absolute;
              left: 50%;
              bottom: -2px;
              height: 10px;
              width: 10px;
              z-index: -1;
              background: #315c99;
              transform: translateX(-50%) rotate(45deg);
            }
          }
        }
      }
    }
  }

  .competences-container-carre {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 50px;
    margin: 80px 20px 0 20px;

    .competence-carre {
      background-color: #fff;
      width: 150px;
      height: 130px;
      padding: 20px 10px 0 10px;
      border-radius: 15px;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

      p {
        font-weight: 600;
      }

      &:hover {
        animation: slide-top 0.5s ease-in-out both;
      }
    }
  }
}

@keyframes progress {
  0% {
    width: 0;
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slide-top {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-20px);
  }
}
